.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  color: $dark-grey-font;
  background-color: $white;
}

.page__body a {
  text-decoration: none;
}
